<template>
  <div class="movepay">
    <div class="container">
      <img src="../../assets/movepay/banner-pay.png" alt="">
    </div>
    <div class="detail">
      <div class="row">
        <img src="../../assets/movepay/pic-pay-1.png" alt="">
        <div class="right">
          <img src="../../assets/movepay/ic-pay-1.png" alt="">
          <div class="group">
            <div class="title">{{$t('m.movepayAdvantage')}}</div>
            <div class="text">{{$t('m.movepayText')}}</div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="right left">
          <img src="../../assets/movepay/ic-pay-2.png" alt="">
          <div class="group">
            <div class="title">{{$t('m.movepayAdvantage1')}}</div>
            <div class="text">{{$t('m.movepayText1')}}</div>
          </div>
        </div>
        <img src="../../assets/movepay/pic-pay-2.png" alt="">
      </div>
      <div class="row">
        <img src="../../assets/movepay/pic-pay-3.png" alt="">
        <div class="right">
          <img src="../../assets/movepay/ic-pay-3.png" alt="">
          <div class="group">
            <div class="title">{{$t('m.movepayAdvantage2')}}</div>
            <div class="text">{{$t('m.movepayText2')}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="ques">
        <v-title :title="this.$t('m.normalQuestion')" 
               border-color="#2591f0" 
               fontColor="#383838"
               fontWeight="none"
               style="margin-bottom:32px"></v-title>
        <question :question="question"></question>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        question: [
          {
            question: '1、使用移动支付会出现银行卡被盗刷问题吗？',
            answer: '只要在安全的网络环境中使用移动支付，绝不会出现银行卡资金安全问题。我们的应用所使用的技术源自目前顶尖技术团队，经过多方验证，并已在中国、韩国、日本等地区具有成熟市场。'
          },
          {
            question: '2、如果手机不见了怎么办？  ',
            answer: '此时可以拨打官方客服电话冻结账户，或者使用其他手机登录账户修改密码等。'
          },
          {
            question: '3、没有网络可以用移动支付吗？',
            answer: '不可以的。这是一款基于网络环境的支付应用。'
          }
        ]
      }
    },
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>
<style scoped lang="stylus">
  @import '../../common/stylus/mixin.styl'
.movepay
  overflow hidden
  .container
    position relative
    height 500px
    img
      height 500px
      position absolute
      left -360px
  .detail
    width 1208px
    height 1234px
    margin 30px auto 74px auto
    bg-image('../../assets/movepay/bg-pay')
    .row
      margin-bottom 154px
      img
        width 400px
        height 260px
        margin 36px 0 0 82px
      .right
        vertical-align top
        display inline-block
        img
          width 60px 
          height 60px
          margin-top 128px
          margin-left 62px
        .group
          width 352px
          margin-top 122px
          margin-left 30px
          display inline-block
          .title
            font-family: PingFangSC;
            font-size: 32px;
            line-height: 1;
            text-align: left;
            color: #2591f0;
          .text
            margin-top 18px
            font-family: MicrosoftYaHei;
            font-size: 16px;
            line-height: 1;
            text-align: left;
            color: #9b9b9b;
      .left
        margin-left 82px
</style>


